.footer {
  background-image: url("../img/footer-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.footer-top {
  padding: 50px 0 130px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__center {
    display: flex;
    margin: 0 30px;
  }
  &__logo {
    display: block;
    width: 237px;
    img {
      width: 100%;
    }
  }
  &__text {
    display: block;
    width: 125px;
    margin-top: 8px;
    img {
      width: 100%;
    }
  }
  &__list {
    &:not(:first-child) {
      margin-left: 100px;
    }
    li {
      font-size: 18px;
      line-height: 120%;
      color: rgba(255, 255, 255, 0.80);
      &:not(:first-child) {
        margin-top: 30px;
      }
      &:hover {
        color: #ffffff;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
  }
  &__social {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(255, 255, 255, 0.10);
    &:not(:first-child) {
      margin-left: 10px;
    }
    &:hover {
      background: rgba(255, 255, 255, 0.10);
      svg path {
        transition: all 0.5s;
      }
      svg path {
        fill: #ffffff;
        fill-opacity: 1;
      }
    }
  }
}
.footer-bottom {
  padding: 15px 0 0 0;
  border-top: 1px solid rgba(255, 255, 255, 0.10);
  &__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    color: #565656;
    margin-bottom: 15px;
    &:not(:last-child) {
      margin-right: 58px;
    }
    &:hover {
      color: #ffffff;
    }
  }
}

@media screen and (max-width: 960px) {
  .footer-top {
    padding: 60px 0;
    &__list {
      &:not(:first-child) {
        margin-left: 60px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      display: block;
    }
    &__center {
      display: block;
      margin: 30px 0;
    }
    &__list {
      &:not(:first-child) {
        margin: 30px 0 0 0;
      }
    }
  }
}