.navigation {
  padding: 50px 0;
  &-list {
    li a {
      font-size: 20px;
      line-height: 120%;
      color: #E1E1E1;
    }
    li:not(:first-child) {
      margin-top: 15px;
    }
    li a:hover {
      opacity: 0.5;
    }
  }
}
.body-bg {
  background: #747474;
}
.content {
  overflow: hidden;
}
.item {
  .hide {
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &--active {
    .hide {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.btn-blue {
  background: #0055F6;
  span {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #FFF;
  }
  &:hover {
    background: #0039b3;
  }
}
.pages {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 120%;
  color: #A7A7A7;
  span {
    padding: 0 3px;
  }
}
.pagination {
  &__items {
    display: flex;
    align-items: center;
  }
  &__item {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    color: #979797;
    &:hover {
      color: #ffffff;
    }
    &--active {
      color: #ffffff;
      border: 1px solid rgba(128, 128, 128, 0.30);
      background: rgba(255, 255, 255, 0.05);
    }
  }
  &__prev,
  &__next {
    svg path {
      transition: all 0.5s;
    }
    &:hover {
      svg path {
        fill-opacity: 1;
      }
    }
  }
}
.text30 {
  font-size: 30px;
  line-height: 120%;
  font-weight: 600;
  letter-spacing: -0.6px;
  color: #ffffff;
}
.text20 {
  font-size: 20px;
  line-height: 120%;
  font-weight: 600;
  color: #ffffff;
}
.content-inner {
  display: flex;
  justify-content: space-between;
  min-height: 830px;
  margin-top: 40px;
  border-top: 1px solid #1B1B1B;
}
.sidebar {
  width: 272px;
  background: #121212;
  padding: 40px 25px;
  position: relative;
  overflow: hidden;
  &-bg {
    span:nth-child(1) {
      content: '';
      position: absolute;
      top: 0;
      right: -420px;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
    span:nth-child(2) {
      content: '';
      position: absolute;
      left: -250px;
      bottom: 0;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
    span:nth-child(3) {
      content: '';
      position: absolute;
      left: -250px;
      bottom: -250px;
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: rgba(31, 188, 255, 0.19);
      filter: blur(107px);
      pointer-events: none;
    }
  }
}
.content-right {
  width: calc(100% - 272px);
  padding: 60px 0 60px 100px;
}
.tab {
  font-size: 16px;
  line-height: 120%;
  color: #FFF;
  cursor: pointer;
  transition: all 0.5s;
  position: relative;
  &:hover {
    color: #5B94FF;
  }
  &--active {
    color: #5B94FF;
  }
  &:not(:first-child) {
    margin-top: 27px;
    &:after {
      content: '';
      position: absolute;
      top: -13px;
      left: 0;
      width: 100%;
      height: 1px;
      background: rgba(255, 255, 255, 0.10);
    }
  }
}
.tabs__content {
  display: none;
  &--active {
    display: block;
  }
}
.modal {
  background: rgba(0, 0, 0, 0.90);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 630px;
    max-width: 100%;
    position: relative;
    background: #151515;
    border: 1px solid rgba(255, 255, 255, 0.10);
    margin: 0 auto;
    padding: 50px 60px;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 18px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    color: #A9A9A9;
    text-align: center;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.6px;
    color: #E1E1E1;
    text-align: center;
    margin-top: 10px;
  }
  &__btns {
    margin-top: 30px;
  }
  &__btn {
    width: 245px;
    height: 60px;
    margin: 0 auto;
  }
  &__cancel {
    font-size: 18px;
    line-height: 120%;
    color: #E1E1E1;
    cursor: pointer;
    text-align: center;
    margin-top: 30px;
    transition: all 0.5s;
    &:hover {
      color: #ffffff;
    }
  }
  .domains-main__info-item {
    width: 11px;
    height: 11px;
    margin-top: 4px;
    position: relative;
  }
  .dashboard-card__active-show {
    color: rgba(255, 255, 255, 0.30);
    cursor: pointer;
    span {
      border-color: rgba(255, 255, 255, 0.30);
      margin: 0;
    }
  }

}
.nice-select {
  height: 50px;
  display: flex;
  align-items: center;
  background: transparent !important;
  border: 1px solid #313133;
  border-radius: 0;
  padding: 5px 32px 5px 20px;
  &:after {
    top: 50%;
    right: 18px;
    transform: translate(0, -50%);
    width: 11px;
    height: 5px;
    background-image: url("../img/select-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  .current {
    font-size: 14px;
    line-height: 120%;
    color: #ffffff;
  }
  .list {
    width: 100%;
    background: #323232;
    filter: drop-shadow(0px 4px 40px rgba(0, 0, 0, 0.70));
    padding: 20px 15px;
    .selected {
      font-weight: 400;
      background: transparent;
      opacity: 0.5;
    }
    li {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      border: none;
      &:hover {
        background: transparent;
        opacity: 0.5;
      }
      &:focus {
        background: transparent;
        opacity: 0.5;
      }
    }
  }
}
.nice-select.open:after {
  transform: rotate(-180deg);
}
.option.focus, .nice-select .option.selected.focus {
  background: transparent;
}
.blue {
  background: #3263C0;
}
.green {
  background: #43B262;
}
.violet {
  background: #AF69C8;
}
.yellow {
  background: #FAC743;
}
.orange {
  background: #FC973F;
}
.red {
  background: #FF524C;
}

@media screen and (max-width: 992px) {
  .content-right {
    padding: 50px 0 50px 50px;
  }
}