.account {
  &__title {
    margin-bottom: 40px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 140%;
    font-weight: 700;
    color: #ffffff;
  }
  &__text {
    font-size: 14px;
    line-height: 140%;
    color: #ffffff;
  }
  .nice-select {
    width: 271px;
    .list {
      width: 120px;
    }
  }
}
.account__info {
  &--active {
    display: none;
  }
  &-items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &-title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.60);
  }
  &-text {
    font-size: 18px;
    line-height: 120%;
    color: #ffffff;
    margin-top: 3px;
  }
  &-btn {
    width: 200px;
    height: 50px;
    margin-top: 40px;
  }
}
.account__form {
  max-width: 660px;
  &-grid {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &-group {
    margin: 0;
    padding: 0;
    border: none;
    input {
      width: 100%;
      height: 50px;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      background: rgba(255, 255, 255, 0.10);
      border: 1px solid transparent;
      padding: 5px 20px;
      background-image: url("../img/input-icon.svg");
      background-repeat: no-repeat;
      background-position: top 50% right 20px;
      background-size: 6px;
      &::placeholder {
        color: rgba(255, 255, 255, 0.50);
      }
      &:focus {
        background-image: none;
      }
    }
  }
  .input-title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.50);
    display: inline-block;
    margin-bottom: 10px;
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
  }
  &-btn {
    width: 200px;
    height: 50px;
  }
  &-btn2 {
    width: 70px;
    height: 50px;
  }
  &-cancel {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #E1E1E1;
    margin-left: 30px;
    cursor: pointer;
    &:hover {
      color: #ffffff;
    }
  }
  &-flex {
    display: flex;
    input {
      width: calc(100% - 162px);
    }
  }
  &-delete {
    width: 152px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #0055F6;
    margin-left: 10px;
    cursor: pointer;
    transition: all 0.5s;
    &:hover {
      background: #0055F6;
    }
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      color: #ffffff;
    }
  }
}
.account__form-none {
  display: none;
  &--active {
    display: block;
  }
}
.api {
  max-width: 912px;
  &-text {
    font-size: 14px;
    line-height: 140%;
    color: #ffffff;
  }
  &-key {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border: 1px dashed #454545;
    padding: 20px 25px;
    margin-top: 40px;
  }
  &-key__desc {
    margin: 0 30px 15px 0;
  }
  &-key__title {
    font-size: 16px;
    line-height: 120%;
    color: #7D7D7D;
  }
  &-key__text {
    display: flex;
    align-items: center;
    margin-top: 10px;
    span {
      font-size: 16px;
      line-height: 140%;
      color: #FFF;
      margin-right: 10px;
    }
    i svg path {
      transition: all 0.5s;
    }
    svg {
      cursor: pointer;
      &:hover {
        path,
        rect {
          stroke: #ffffff;
        }
      }
    }
  }
  &-key__btn {
    width: 150px;
    height: 30px;
    margin-bottom: 15px;
    span {
      font-size: 16px !important;
    }
  }
}
.sessions {
  max-width: 780px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 25px;
    border: 1px dashed #454545;
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 20px 20px 0 20px;
    &:not(:first-child) {
      border-top: 1px dashed #454545;
    }
    &-desc {
      margin-right: 30px;
      margin-bottom: 20px;
    }
    &-text {
      p {
        font-size: 16px;
        line-height: 140%;
        color: #ffffff;
      }
    }
    &-account {
      font-size: 16px;
      line-height: 140%;
      color: #ffffff;
      margin-top: 12px;
    }
    &-btn {
      width: 110px;
      height: 45px;
      background: transparent;
      border: 1px solid #0055F6;
      margin-bottom: 20px;
      &:hover {
        background: #0055F6;
      }
      span {
        font-size: 18px;
        line-height: 120%;
        font-weight: 600;
        color: #ffffff;
      }
    }
  }
}
.configuring {
  max-width: 925px;
  &__group {
    &:not(:first-child) {
      margin-top: 40px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 5px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -20px;
      width: 100%;
      height: 1px;
      background: #373737;
    }
    &-left {
      display: flex;
      align-items: center;
      margin-right: 30px;
    }
    &-check {
      display: flex;
      align-items: center;
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 35px;
        height: 18px;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          background: #0055F6;
          &:after {
            left: calc(100% - 15px);
            background: #ffffff;
          }
        }
        &:checked ~ .configuring__check-text {
          span:nth-child(1) {
            display: none;
          }
          span:nth-child(2) {
            display: block;
          }
        }
      }
      label {
        width: 35px;
        height: 18px;
        background: #000000;
        border-radius: 20px;
        position: relative;
        transition: all 0.5s;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 3px;
          transform: translate(0,-50%);
          width: 12px;
          height: 12px;
          background: #606060;
          border-radius: 50%;
          transition: all 0.5s;
        }
      }
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      color: #FFF;
      margin-left: 60px;
    }
    &-btn {
      width: 20px;
      height: 12px;
      cursor: pointer;
      position: relative;
      span {
        position: absolute;
        left: 0;
        width: 100%;
        height: 1.3px;
        background: #ffffff;
      }
      span:nth-child(1) {
        top: 0;
      }
      span:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      span:nth-child(3) {
        bottom: 0;
      }
    }
  }
  &__check-text {
    min-width: 25px;
    margin-left: 5px;
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #3F4043;
    }
    span:nth-child(1) {
      color: #3F4043;
    }
    span:nth-child(2) {
      color: #ffffff;
      display: none;
    }
  }
}
.user {
  max-width: 780px;
  .nice-select {
    .list {
      width: 100%;
    }
  }
}
.user-top {
  margin-top: 30px;
}
.user-bottom {
  display: grid;
  grid-template-columns: 100%;
  gap: 12px;
  margin-top: 30px;
}
.user-form {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  &:not(:first-child) {
    margin-top: 30px;
  }
}
.user-form__left {
  display: flex;
  align-items: center;
  margin: 0 6px 15px 0;
  .nice-select {
    width: 197px;
  }
}
.user-form__right {
  display: flex;
  align-items: flex-end;
  margin-bottom: 15px;
  .nice-select {
    width: 237px;
  }
}
.input-group {
  width: 265px;
}
.user-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border: 1px dashed #454545;
  padding: 20px 25px 0 25px;
}
.user-item__desc {
  margin-right: 30px;
  margin-bottom: 15px;
}
.user-item__title {
  font-size: 16px;
  line-height: 120%;
  color: #7D7D7D;
}
.user-item__text {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 10px;
}
.user-item__link {
  font-size: 16px;
  line-height: 120%;
  color: #ffffff;
  border-right: 1px solid #ffffff;
  padding-right: 12px;
  margin-bottom: 10px;
  &:hover {
    opacity: 0.5;
  }
}
.user-item__info {
  font-size: 16px;
  line-height: 120%;
  color: #ffffff;
  padding-left: 12px;
  margin-bottom: 10px;
}
.user-item__btn {
  width: 110px;
  height: 45px;
  background: transparent;
  border: 1px solid #0055F6;
  margin-bottom: 15px;
  &:hover {
    background: #0055f6;
  }
  span {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
  }
}
