.header {
  padding: 15px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 30px;
  }
  &__logo {
    display: block;
    width: 44px;
    img {
      width: 100%;
    }
  }
  &__check {
    display: flex;
    align-items: center;
    width: 38px;
    height: 21px;
    position: relative;
    margin-left: 30px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked ~ label {
        background: #0055F6;
        border-color: #0055f6;
        &:after {
          left: calc(100% - 20px);
          background: #0A0A0A;
        }
        &:before {
          left: calc(100% - 16px);
        }
      }
    }
    label {
      width: 100%;
      height: 100%;
      border-radius: 10.615px;
      border: 0.885px solid rgba(255, 255, 255, 0.15);
      background: rgba(255, 255, 255, 0.06);
      margin: 0;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: -1px;
        width: 21px;
        height: 21px;
        background: rgba(255, 255, 255, 0.40);
        border-radius: 50%;
        transition: all 0.5s;
      }
      &:before {
        content: '';
        position: absolute;
        top: 48%;
        left: 2px;
        transform: translate(0, -50%);
        width: 14px;
        height: 14px;
        background-image: url("../img/check-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transition: all 0.5s;
      }
    }
  }
  &__list {
    display: flex;
    align-items: center;
    margin-left: 30px;
  }
  &__link {
    font-size: 18px;
    line-height: 120%;
    color: #E1E1E1;
    &:not(:first-child) {
      margin-left: 25px;
    }
    &--active {
      a {
        opacity: 0.5;
      }
    }
    &:hover {
      a {
        opacity: 0.5;
      }
    }
  }
  &__money {
    display: flex;
    align-items: center;
    &-icon {
      width: 16px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #E1E1E1;
      margin-left: 6px;
    }
  }
  &__notification {
    position: relative;
    margin-left: 30px;
    &-icon {
      position: relative;
      svg {
        width: 19px;
        height: auto;
      }
      span {
        position: absolute;
        right: -2px;
        bottom: 0;
        width: 6.7px;
        height: 6.7px;
        border-radius: 50%;
      }
    }
    &-hide {
      position: absolute;
      top: 100%;
      right: -30px;
      width: 350px;
      padding-top: 15px;
    }
    &-wrap {
      background: #2E2E2E;
      padding: 15px 0;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -18px;
        right: 25px;
        border: 10px solid transparent;
        border-bottom: 10px solid #2E2E2E;
        pointer-events: none;
      }
    }
    &-items {
      padding: 0 15px;
      height: 235px;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 2px;
      }
      &::-webkit-scrollbar-track {
        background: #444;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #000000;
        border-radius: 3px;
      }
    }
    &-item {
      &:not(:last-child) {
        border-bottom: 1px solid rgba(0, 0, 0, 0.30);
        padding-bottom: 10px;
        margin-bottom: 10px;
      }
    }
    &-subtitle {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      color: #ffffff;
      margin-top: 5px;
    }
    &-btn {
      width: 152px;
      height: 41px;
      background: transparent;
      border: 1px solid #0055F6;
      margin: 25px auto 0;
      span {
        font-size: 14px;
        line-height: 120%;
        color: #ffffff;
      }
      &:hover {
        background: #0055F6;
      }
    }
  }
  &__user {
    position: relative;
    margin-left: 20px;
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      &:hover {
        .header__user-text {
          color: #ffffff;
        }
        .header__user-arrow {
          transform: rotate(-180deg);
          svg path {
            fill-opacity: 1;
          }
        }
      }
    }
    &-hide {
      position: absolute;
      top: 100%;
      right: 0;
      width: 167px;
      padding-top: 15px;
    }
    &-wrap {
      background: #2E2E2E;
      padding: 15px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -18px;
        right: 25px;
        border: 10px solid transparent;
        border-bottom: 10px solid #2E2E2E;
        pointer-events: none;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #E1E1E1;
    }
    &-icons {
      display: flex;
      align-items: center;
      margin-left: 8px;
    }
    &-avatar {
      width: 29px;
      img {
        width: 100%;
      }
    }
    &-arrow {
      width: 11px;
      margin-left: 8px;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.5s;
      }
    }
    &-links {
      padding-bottom: 15px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.30);;
    }
    &-link {
      display: block;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:hover {
        opacity: 0.5;
      }
    }
    &-exit {
      display: block;
      font-size: 16px;
      line-height: 120%;
      color: #A9A9A9;
      padding-top: 15px;
      &:hover {
        color: #ffffff;
      }
    }
  }
  .item--active {
    .header__user-text {
      color: #ffffff;
    }
    .header__user-arrow {
      transform: rotate(-180deg);
      svg path {
        fill-opacity: 1;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__right {
      margin-left: 20px;
    }
    &__check {
      margin-left: 20px;
    }
    &__list {
      margin-left: 20px;
    }
    &__link {
      font-size: 16px;
    }
    &__money {
      &-text {
        font-size: 14px;
      }
    }
    &__notification {
      margin-left: 20px;
    }
    &__user {
      &-text {
        display: none;
      }
      &-icons {
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
   .header {
     &__inner {
       align-items: start;
     }
     &__left {
       display: block;
       align-items: start;
     }
     &__right {
       display: block;
     }
     &__check {
       margin: 15px 0 0 0;
     }
     &__list {
       display: block;
       margin: 15px 0 0 0;
     }
     &__link {
       &:not(:first-child) {
         margin: 15px 0 0 0;
       }
     }
     &__notification {
       margin: 15px 0 0 0;
       &-show {
         display: flex;
         justify-content: flex-end;
       }
       &-hide {
         right: 0;
       }
       &-icon {
         display: inline-block;
       }
     }
     &__user {
       margin: 15px 0 0 0;
       &-show {
         justify-content: flex-end;
       }

     }
   }
}